import Swiper, {  Pagination } from 'swiper';
const slider = () => {
    const swiperGallery = new Swiper('.shop__swiper', {
        modules: [Pagination],
        loop: true,
        slidesPerView: 'auto',
        speed: 1000,
        pagination: {
            el: '.shop__pagination',
            clickable: true,
        },
    });
    const reviewsGallery = new Swiper('.reviews__swiper', {
        modules: [Pagination],
        loop: true,
        slidesPerView: 3,
        spaceBetween: 135,
        freeMode: true,
        speed: 1000,
        centeredSlides: true,
        pagination: {
            el: '.reviews__pagination',
            clickable: true,
        },
        breakpoints: {
            280: {
                slidesPerView: 1,
            },
            732: {
                slidesPerView: 2,
            },
            1262: {
                slidesPerView: 3,
            }
        }
    });
}
export default slider